import * as React from "react";
import {
  AppBar,
  Grid,
  Link,
  Toolbar,
  ListItem,
  ListItemText,
  Typography,
  Button,
  Hidden,
} from "@material-ui/core";
import capreslogo from "../../assets/images/capres.jpg";
import { makeStyles } from "@material-ui/core/styles";
import { Link as RouterLink } from "react-router-dom";
import SideDrawer from "./SideDrawer";
const navLinks = [
  {
    title: `Nosotros`,
    path: `/nosotros`,
  },
  {
    title: `Servicios`,
    path: `/servicios`,
  },
  {
    title: `Socios`,
    path: `/socios`,
  },
  {
    title: `Contacto`,
    path: `/contacto`,
  },
];

const useStyles = makeStyles((theme) => ({
  styledLogo: {
    marginTop: "4px",
    width: "64px",
    height: "64px",
  },
  styledTypography: {
    "& .MuiTypography-body1": {
      color: theme.palette.common.capresGreyLight,
    },

    textTransform: "uppercase",
    fontWeight: "bold",
  },
  styledLoginButton: {
    "& .MuiButton-label": {
      color: theme.palette.common.capresBlue,
      fontWeight: 600,
      fontSize: "1rem",
    },

    borderRadius: "0px",
    marginRight: "25px",
    fontFamily: "Montserrat",
    height: "45px",
    width: "10rem",

    "&:hover": {
      backgroundColor: theme.palette.primary.light,
      "& .MuiButton-label": {
        color: theme.palette.common.capresOrange,
      },
    },
  },
  styledListItem: {
    "&:hover": {
      backgroundColor: "transparent",
    },
  },
}));

const Header4 = () => {
  const classes = useStyles();
  return (
    <AppBar position="static">
      <Toolbar>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
        >
          <Grid item>
            <Link href="/">
              <img
                className={classes.styledLogo}
                src={capreslogo}
                alt="capreslogo"
              />
            </Link>
          </Grid>
          <Hidden mdDown>
            <Grid item>
              <Grid container spacing={4}>
                {navLinks.map(({ title, path }) => (
                  <Grid item key={title}>
                    <RouterLink
                      to={path}
                      key={title}
                      style={{ textDecoration: "none" }}
                    >
                      <ListItem className={classes.styledListItem} button>
                        <ListItemText
                          className={classes.styledTypography}
                          disableTypography
                          primary={
                            <Typography
                              type="body2"
                              style={{ fontWeight: "bold" }}
                            >
                              {title}
                            </Typography>
                          }
                        />
                      </ListItem>
                    </RouterLink>
                  </Grid>
                ))}
              </Grid>
            </Grid>
          </Hidden>

          <Grid item>
            <Button
              className={classes.styledLoginButton}
              variant="contained"
              color="secondary"
              component={RouterLink}
              to="/login"
            >
              Login
            </Button>
          </Grid>

          <Hidden mdUp>
            <SideDrawer navLinks={navLinks} />
          </Hidden>
        </Grid>
      </Toolbar>
    </AppBar>
  );
};

export default Header4;
